const checkValidity=(value, rules)=> {
    let isValid = true;
    let erros=[]
    if (!rules) {
        return true;
    }
    
    if (rules.required) {
        isValid = value.trim() !== '' && isValid;
        if(!isValid){
            erros.push('required');
        } 
    }
    if (rules.minLength) {
        isValid = value.length >= rules.minLength && isValid;
        if(!isValid){
        erros.push('minLength');}
    }

    if (rules.maxLength) {
        isValid = value.length <= rules.maxLength && isValid;
        if(!isValid){ erros.push('maxLength')};
    }

    if (rules.isEmail) {
        const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
        isValid = pattern.test(value) && isValid
        if(!isValid){
            erros.push('isEmail');
        }
    }

    if (rules.isPassword) {
        const pwdPattern = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%]).{6,20}/;
        isValid = pwdPattern.test(value) && isValid;
        if(!isValid){ erros.push('isPassword')};
    }

    if (rules.isNumeric) {
        const pattern = /^\d+$/;
        isValid = pattern.test(value) && isValid;
        if(!isValid){erros.push('isNumeric')};
    }

    return {
        isValid: isValid,
        erros:erros
    } 

}      


export  default checkValidity;


