import React, { Component } from 'react';
import styleClasses from  './Login.css';
import LockIcon from '@material-ui/icons/LockOutlined';
import Avatar from '@material-ui/core/Avatar';
import withStyles from '@material-ui/core/styles/withStyles';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Button from '@material-ui/core/Button';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import InputAdornment from '@material-ui/core/InputAdornment';
import IconButton from '@material-ui/core/IconButton';
import checkValidity from '../../../core/validate.default';
import FormHelperText from '@material-ui/core/FormHelperText';
import { connect } from 'react-redux';
import * as actions from '../../../store/actions/index';
import Spinner from '../../../components/spinner/Spinner';
import { Redirect } from 'react-router-dom';
const styles = theme => ({
    layout: {
      width: 'auto',
      display: 'block', // Fix IE11 issue.
      marginLeft: theme.spacing.unit * 3,
      marginRight: theme.spacing.unit * 3,
      [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
        width: 400,
        marginLeft: 'auto',
        marginRight: 'auto',
      },
    },
    paper: {
      marginTop: theme.spacing.unit * 8,
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
    },
    avatar: {
      margin: theme.spacing.unit,
      backgroundColor: theme.palette.primary.main,
    },
    form: {
      width: '100%', // Fix IE11 issue.
      marginTop: theme.spacing.unit,
    },
    submit: {
      marginTop: theme.spacing.unit * 3,
    },
  });
  
class Login extends Component {
 state={
  signInForm: {

    password: {
            placeholder: 'password',
        value: '',
        validation: {
            required: true,
            // isPassword:true
        },
        valid: false,
        touched: false,
        erros:[]
    },

    email: {
        placeholder: 'email',
        value: '',
        validation: {
            required: true,
            // isEmail: true
        },
        valid: false,
        touched: false,
        erros:[]
    },

},
formIsValid: false,
showPassword: false
 }


//  handle mouse down event
 handleMouseDownPassword = event => {
  event.preventDefault();
};

// show password in text form
handleClickShowPassword = () => {
  this.setState(state => ({ showPassword: !state.showPassword }));
};


 handleSubmit(event, props) {
      event.preventDefault();
      let user={
        email:this.state.signInForm.email.value,
        password:this.state.signInForm.password.value

      }

      this.props.onLogin(user);
 
      }
   
      

// handle input change   
handleInputChange(event, inputName){
 let updatedLoginForm = {...this.state.signInForm};
 let updateFormEle={...updatedLoginForm[inputName]}
 updateFormEle.value=event.target.value;
updateFormEle.valid = checkValidity(updateFormEle.value, updateFormEle.validation).isValid;
updateFormEle.erros = checkValidity(updateFormEle.value, updateFormEle.validation).erros;
 updateFormEle.touched = true;
 updatedLoginForm[inputName] = updateFormEle;
 let formIsValid = true;
 for (let inputName in updatedLoginForm) {
  formIsValid = updatedLoginForm[inputName].valid && formIsValid;
}
 this.setState({signInForm:updatedLoginForm,formIsValid: formIsValid})
}
  componentDidMount() {
      if (true) {
       
          this.props.onSetAuthRedirectPath();
      }
  }
  render() {
    let loadingContainer="";
    if(this.props.loading){
      loadingContainer =(<Spinner/>)
    }
    
    const { classes } = this.props;
   let authRedirect = null;
        if (this.props.isAuthenticated) {
            authRedirect = <Redirect to={this.props.authRedirectPath}/>
        }
    return (
    
     <div className={styleClasses.LoginWrapper}>
     {authRedirect}
     <div className={styleClasses.LoginContainer}>
 
     <div className={styleClasses.spinerWrapper}>
     {loadingContainer}
     </div>
     <Avatar className={classes.avatar}>
     <LockIcon />
   </Avatar>
   <Typography variant="headline">Sign in</Typography>
   <form method="post"  className={classes.form} onSubmit={(event) => this.handleSubmit(event,this.props)}>
            <FormControl margin="normal" required fullWidth className={ (!this.state.signInForm.email.valid && this.state.signInForm.email.touched) ? 'error' : '' }>
              <InputLabel htmlFor="email">{this.state.signInForm.email.placeholder}</InputLabel>
              <Input id="email" 
              onChange={(event) => this.handleInputChange(event, "email")} 
              value={this.state.signInForm.email.value} name="email"
              touched={(this.state.signInForm.email.touched).toString()}
              invalid={(!this.state.signInForm.email.valid).toString()}
               autoFocus />
               
               <div>
               {(this.state.signInForm.email.erros).map(function(errType, idx) {
                   return <div key={idx}><FormHelperText error>
                   {errType==='required'?"Email is Required":"Email Address is not right"
               }
                  
                   </FormHelperText></div>;
               })}
             
              </div>
            
            </FormControl>
            <FormControl required fullWidth margin="normal"  className={ (!this.state.signInForm.password.valid && this.state.signInForm.password.touched) ? 'error' : '' }>
              <InputLabel htmlFor="password">{this.state.signInForm.password.placeholder}</InputLabel>
              
              <Input
                name="password"
                type={this.state.showPassword ? 'text' : 'password'}
                id="password"
                onChange={(event) => this.handleInputChange(event, "password")} 
                value={this.state.signInForm.password.value}
                touched={(this.state.signInForm.password.touched).toString()}
                invalid={(!this.state.signInForm.password.valid).toString()}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="Toggle password visibility"
                      onClick={this.handleClickShowPassword}
                      onMouseDown={this.handleMouseDownPassword}
                    >
                      {this.state.showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
              />
              <div>
              {(this.state.signInForm.password.erros).map(function(errType, idx) {
                  return <div key={idx}><FormHelperText error>
                  {errType==='required'?"Password is Required":"password should contain atleast 1 upper case, 1 special char and 1 numeric val"
              }
                 
                  </FormHelperText></div>;
              })}
            
             </div>
            </FormControl>
            <Button disabled={!this.state.formIsValid}
              type="submit"
              fullWidth
              variant="raised"
              color="primary"
              className={classes.submit}
            >
              Sign in
            </Button>
          </form>
     </div></div>
    );
  }
}
Login.propTypes = {
    classes: PropTypes.object.isRequired,
  };

const mapStateToProps = state => {
  return {
      authDetail: state.login.authDetail,
      loading: state.login.loading,
      isAuthenticated: state.login.token !== null,
       authRedirectPath: state.login.authRedirectPath
        
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onLogin: (user) => dispatch( actions.login(user) ),
    onSetAuthRedirectPath: () => dispatch(actions.setAuthRedirectPath('/home'))
  };
};

export default connect( mapStateToProps, mapDispatchToProps)( withStyles(styles)( Login) );


